<template>
    <div class="msg-template">
        <a-card>
            <a-row :style="{display:device == 'mobile' ? 'block' : 'flex'}">
                <a-col :style="{width: device == 'mobile' ? '100%' : '75%'}">
                    <a-form :form="form">
                        <a-form-item>
                        <a-textarea :autosize="{ minRows: 18,maxRows:25 }" v-decorator="['msgContent',{initialValue:msgInitValue}]"></a-textarea>
                        </a-form-item>
                    </a-form>
                </a-col>
                <a-col :style="{ width: device == 'mobile' ? '100%' : '25%',marginLeft:device == 'mobile' ? '0' : '24px'}">
                    <div class="checkable-tag" style="margin-bottom:16px">
                        <strong :style="{ marginRight: 8 ,marginBottom:16}">模板标签:</strong>
                        <br/>
                        <div style="margin-top:16px">
                            <template v-for=" tag in tags">
                                <a-checkable-tag
                                :key="tag"
                                :checked="selectedTag.indexOf(tag) > -1"
                                @change="(checked) => handleChange(tag, checked)"
                                >
                                {{tag}}
                                </a-checkable-tag>
                            </template>
                        </div>
                        <a-input :value="selectedTagValue" />
                    </div>
                </a-col>
                
                </a-row>
            </a-card>
        </div>
</template>

<script>
    import AForm from "ant-design-vue/lib/form";
    import ACard from "ant-design-vue/lib/card";
    import ATag from "ant-design-vue/lib/tag";
    import AInput from "ant-design-vue/lib/input";
    import ARow from 'ant-design-vue/lib/row'
    import ACol from 'ant-design-vue/lib/col'
    import WangEditor from "@/components/Tools/Wangeditor";
    import {
        mixinDevice
    } from '@/utils/mixin.js'
    const ACheckableTag = ATag.CheckableTag;
    const ATextarea = AInput.TextArea;
    const AFormItem = AForm.Item;
    export default {
        components: {
            ACard,
            ACheckableTag,
            AInput,
            ATextarea,
            ATag,
            AForm,
            AFormItem,
            ARow,
            ACol,
            WangEditor
        },
        mixins: [mixinDevice],
        data() {
            return {
                checked1: false,
                checked2: false,
                checked3: false,
                tags: ["客户姓名", "公司代表人", "工号", "车牌号", "商业险费用", "车损险费用", "盗抢险费用", "三者险费用", "司机险费用", "乘客险费用", "划痕险费用", "自燃险费用", "破碎险费用", "设备险费用", "不计免赔总额", "交强险费用", "车船税费用", "含税总计费用"],
                selectedTag: ["客户姓名"],
                msgInitValue: '尊敬的{$INFO.AUTO.OWNER}先生/女士您好！我是客户经理{$INFO.USER.NAME}，工号{$INFO.USER.WORKNO}，为您的爱车（{$INFO.AUTO.LICENSE_NO}）车险报价：商业险{$INFO.POLICY.BUSINESS_PREMIUM}元（{if $INFO.POLICY.TVDI_INSURANCE_AMOUNT neq ""}含车损{$INFO.POLICY.TVDI_INSURANCE_AMOUNT}元，{/if}{if $INFO.POLICY.TWCDMVI_INSURANCE_AMOUNT neq ""}盗抢险{$INFO.POLICY.TWCDMVI_INSURANCE_AMOUNT}元，{/if}{if $INFO.POLICY.TTBLI_INSURANCE_AMOUNT neq ""}三者{$INFO.POLICY.TTBLI_INSURANCE_AMOUNT}万元，{/if}{if $INFO.POLICY.TCPLI_INSURANCE_DRIVER_AMOUNT neq ""}司机险{$INFO.POLICY.TCPLI_INSURANCE_DRIVER_AMOUNT}元，{/if}{if $INFO.POLICY.TCPLI_INSURANCE_PASSENGER_AMOUNT*$INFO.POLICY.TCPLI_PASSENGER_COUNT neq ""}乘客险{$INFO.POLICY.TCPLI_INSURANCE_PASSENGER_AMOUNT*$INFO.POLICY.TCPLI_PASSENGER_COUNT}元，{/if}{if $INFO.POLICY.BSDI_INSURANCE_AMOUNT neq ""}划痕险{$INFO.POLICY.BSDI_INSURANCE_AMOUNT}元，{/if}{if $INFO.POLICY.SLOI_INSURANCE_AMOUNT neq ""}自燃险{$INFO.POLICY.SLOI_INSURANCE_AMOUNT}元，{/if}{if $INFO.POLICY.BGAI_PREMIUM neq ""}破碎险{$INFO.POLICY.BGAI_PREMIUM}元，{/if}{if $INFO.POLICY.NIELI_INSURANCE_AMOUNT neq ""}设备险{$INFO.POLICY.NIELI_INSURANCE_AMOUNT}元，{/if}（不计免赔{$INFO.POLICY.TVDI_NDSI_PREMIUM+$INFO.POLICY.TTBLI_NDSI_PREMIUM+$INFO.POLICY.TWCDMVI_NDSI_PREMIUM+$INFO.POLICY.TCPLI_DRIVER_NDSI_PREMIUM+$INFO.POLICY.TCPLI_PASSENGER_NDSI_PREMIUM+$INFO.POLICY.BSDI_NDSI_PREMIUM+$INFO.POLICY.SLOI_NDSI_PREMIUM+$INFO.POLICY.NIELI_NDSI_PREMIUM+$INFO.POLICY.VWTLI_NDSI_PREMIUM}）{if $INFO.POLICY.TOTAL_MVTALCI_PREMIUM neq ""}交强险{$INFO.POLICY.TOTAL_MVTALCI_PREMIUM}元，{/if}{if $INFO.POLICY.TOTAL_TRAVEL_TAX_PREMIUM neq ""}车船税{$INFO.POLICY.TOTAL_TRAVEL_TAX_PREMIUM}元，{/if}{if $INFO.POLICY.TOTAL_PREMIUM+$INFO.POLICY.TOTAL_TRAVEL_TAX_PREMIUM neq ""}含税总计{$INFO.POLICY.TOTAL_PREMIUM}元{/if}。最终价格请以出单为准。回T退订',              
                tagsMap:{
                    "客户姓名":"{$INFO.AUTO.OWNER}",
                    "公司代表人":"{$INFO.USER.NAME}",
                    "工号":"{$INFO.USER.WORKNO}",
                    "车牌号":"{$INFO.AUTO.LICENSE_NO}",
                    "商业险费用":"{$INFO.POLICY.BUSINESS_PREMIUM}",
                    "车损险费用":"{$INFO.POLICY.TVDI_INSURANCE_AMOUNT}",
                    "盗抢险费用":"{$INFO.POLICY.TWCDMVI_INSURANCE_AMOUNT}",
                    "三者险费用":"{$INFO.POLICY.TTBLI_INSURANCE_AMOUNT}",
                    "司机险费用":"{$INFO.POLICY.TCPLI_INSURANCE_DRIVER_AMOUNT}",
                    "乘客险费用":"{$INFO.POLICY.TCPLI_INSURANCE_PASSENGER_AMOUNT*$INFO.POLICY.TCPLI_PASSENGER_COUNT}",
                    "划痕险费用":"{$INFO.POLICY.BSDI_INSURANCE_AMOUNT}",
                    "自燃险费用":"{$INFO.POLICY.SLOI_INSURANCE_AMOUNT}",
                    "破碎险费用":"{$INFO.POLICY.BGAI_PREMIUM}",
                    "设备险费用":"{$INFO.POLICY.NIELI_INSURANCE_AMOUNT}",
                    "不计免赔总额":"{$INFO.POLICY.TVDI_NDSI_PREMIUM+$INFO.POLICY.TTBLI_NDSI_PREMIUM+$INFO.POLICY.TWCDMVI_NDSI_PREMIUM+$INFO.POLICY.TCPLI_DRIVER_NDSI_PREMIUM+$INFO.POLICY.TCPLI_PASSENGER_NDSI_PREMIUM+$INFO.POLICY.BSDI_NDSI_PREMIUM+$INFO.POLICY.SLOI_NDSI_PREMIUM+$INFO.POLICY.NIELI_NDSI_PREMIUM+$INFO.POLICY.VWTLI_NDSI_PREMIUM}",
                    "交强险费用":"{$INFO.POLICY.TOTAL_MVTALCI_PREMIUM}",
                    "车船税费用":"{$INFO.POLICY.TOTAL_TRAVEL_TAX_PREMIUM}",
                    "含税总计费用":"{$INFO.POLICY.TOTAL_PREMIUM}"
                },
            };
        },
        beforeCreate() {
            this.form = this.$form.createForm(this);
        },
        computed:{
            selectedTagValue (){
                return this.tagsMap[this.selectedTag]
            },
        },
        methods: {
            log(e) {
                console.log(e);
            },
            handleChange(tag, checked) {
                const {
                    selectedTag
                } = this;
                // const nextSelectedTags = checked ? [tag] :
                //     selectedTag.filter(t => t !== tag);
                const nextSelectedTags = [tag]
                console.log("You are interested in: ", nextSelectedTags);
                this.selectedTag = nextSelectedTags;
            },
        }
    };
</script>

<style scope>
    .msg-template .checkable-tag>div>div {
        font-size: 14px;
        margin-bottom: 16px;
    }
</style>



